body{
       margin : 0;
       font-family: Verdana, Geneva, Tahoma, sans-serif;
       background: linear-gradient(to right,gray,black);
}
button {
       cursor: pointer;
       display: block;
       width:350px;
       padding: 10px;
       margin: auto;
       margin-bottom: 15px;
       color: white;
       border: none;
       border-radius: 3px;
}
.img {
       max-width: 400px;
       max-height: 400px;
       margin: auto;
       margin-top: 50px;
       display: block;
       box-shadow: 0px 0px 10px black;
}
input {
       box-sizing: border-box;
       display: block;
       padding: 10px;
       width: 350px;
       margin: auto;
       margin-bottom: 15px;
}
h1 {
       text-align: center;
       font-size: 50px;
       text-shadow: 0px 0px 5px black;

}
.container {
       margin-top: 50px;
}
.Next {
       background-color: #f0ad4e;
}

